/* Cachons la case à cocher */
[type="checkbox"]:not(:checked),
[type="checkbox"]:checked {
  position: absolute;
  left: -9999px;
}
/* on prépare le label */
[type="checkbox"]:not(:checked) + label,
[type="checkbox"]:checked + label {
  position: relative; /* permet de positionner les pseudo-éléments */
  padding:15px 15px 15px 25px; /* fait un peu d'espace pour notre case à venir */
  cursor: pointer;    /* affiche un curseur adapté */
  font-size:1em;
}

/* Aspect des checkboxes */
/* :before sert à créer la case à cocher */
[type="checkbox"]:not(:checked) + label:before,
[type="checkbox"]:checked + label:before {
  content: '';
  position: absolute;
  left:0; top: 13px;
  width: 17px; height: 17px; /* dim. de la case */
  border: 1px solid #aaa;
  background: #f8f8f8;
  border-radius: 3px; /* angles arrondis */
  box-shadow: inset 0 1px 3px rgba(0,0,0,.3) /* légère ombre interne */
}
/* Aspect général de la coche */
[type="checkbox"]:not(:checked) + label:after,
[type="checkbox"]:checked + label:after {
  content: '\2714';
  position: absolute;
  top: 14px; left: 4px;
  font-size: 14px;
  color: #09ad7e;
  transition: all .2s; /* on prévoit une animation */
}
/* Aspect si "pas cochée" */
[type="checkbox"]:not(:checked) + label:after {
  opacity: 0; /* coche invisible */
  -webkit-transform: scale(0);
          transform: scale(0); /* mise à l'échelle à 0 */
}
/* Aspect si "cochée" */
[type="checkbox"]:checked + label:after {
  opacity: 1; /* coche opaque */
  -webkit-transform: scale(1);
          transform: scale(1); /* mise à l'échelle 1:1 */
}




.sticky-header {
  color:#666;
  border-bottom:1px solid #bbb;
  background:#f8f8f8;/* 4bb861 - 76508f */
  box-shadow:0 5px 0 rgba(0,0,0,.1);
  position:fixed;
  width:100%;
  top:0;
  left:0;
  z-index:10;
  text-align:right;
  padding:20px 0;
}
.sticky-header button {
  cursor:pointer;
  color:white;
  margin-left:15px;
  font-size:1.1em;
  padding:10px 15px;
  border-radius:4px;
  cursor:pointer;
  font-weight:bold;
  color:#bbb;
  border:1px solid #ddd;
  background:#f8f8f8;/* 4bb861 - 76508f */
  cursor:pointer;
}
.sticky-header button:last-child {
  background:#4bb861;
  border:1px solid #4bb861;
  color:white;
  padding:10px 20px;
  box-shadow: 5px 5px 0 rgba(0,0,0,.1);
}
.sticky-header button:last-child:disabled {
  opacity:.4;
  -webkit-filter:grayscale(1);
          filter:grayscale(1);
}
.sticky-header button.facebookColor {
  background:#3B5998;
  border:1px solid #3B5998;
  color:white;
}
.ticky-header__title {
