  height: 100px;
  position: relative;
  cursor: pointer;
  margin: 0 0 20px 0;
  overflow: hidden;
}
.product__image {
  position: absolute;
  width: 100px;
  height: 100px;
  top: 0;
  left: 0;
  background-size: cover;
  background-position: center center;
}
.product__desc {padding:10px 10px 0 0;}
.product__desc span:last-child {display:none;}
.product__qty {display:none}
.product__cat {color:#666;background:rgba(0,0,0,.1);padding:2px;margin-top:5px;font-size:.7em;display:inline-block;}
.product__cat span:last-child {display:none;}
.product__price {position:absolute;bottom:10px;right:10px;}
.product__price span:first-child {text-decoration:line-through;margin-right:5px;}
.product__price span:last-child {margin-left:5px;}

.product.selected {
  border: 1px solid #4bb861;
  box-shadow:5px 5px 0 #4bb861;
  background:#ddf2e1;
}

